/* Basic content level html elements go here */
body {
  font-family: "Raleway", sans-serif;
  font-size: calc(10px + 1.5vmin);
  line-height: 1.4; }

h2 {
  margin-bottom: calc(1rem + 3vmin);
  text-align: center; }
  h2:after {
    border-top: 1px solid black;
    content: "";
    display: block;
    margin: calc(1rem + 3vmin) auto 0;
    width: 2rem; }
    @media (min-width: 768px) {
      h2:after {
        border-top: 0.45vmin dotted black; } }

a {
  color: #fb7d00;
  text-decoration: none;
  -webkit-transition: all 300ms ease;
  transition: all 300ms ease; }
  a:hover, a:focus {
    color: #ff1e65;
    text-decoration: underline; }

img {
  display: block;
  max-width: 100%;
  margin: 1em auto; }

button {
  background-color: #fb7d00;
  border: 0 none;
  border-radius: 2.25rem;
  box-shadow: inset 0 0 15px 0 rgba(200, 100, 0, 0.5);
  color: white;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 700;
  padding: 1.25rem 1.75rem;
  text-transform: uppercase;
  -webkit-transition: all 300ms ease;
  transition: all 300ms ease; }
  button:hover, button:focus {
    background-color: #ff1e65;
    box-shadow: inset 0 0 15px 0 rgba(234, 0, 74, 0.5); }

li {
  margin-bottom: 0.5em; }

/* More general layout styling */
main > section {
  padding: 5%; }

/* Styles for the app header */
.app-header {
  align-items: center;
  background-color: #ff1e65;
  color: white;
  display: flex;
  flex-direction: column;
  font-size: 120%;
  justify-content: center;
  min-height: 100vh;
  text-align: center; }
  .app-header h1 {
    font-size: 23vmin;
    font-weight: 200;
    line-height: 0.85;
    margin: auto 0 0.5rem; }
    @media (min-width: 320px) {
      .app-header h1 {
        font-family: "Raleway Dots", sans-serif;
        font-weight: 400; } }
    .app-header h1 + p {
      margin-bottom: 0;
      margin-top: calc(1rem + 3vmin); }
      .app-header h1 + p:before {
        border-top: 1px solid white;
        content: "";
        display: block;
        margin: 0 auto calc(1rem + 3vmin);
        width: 2rem; }
        @media (min-width: 768px) {
          .app-header h1 + p:before {
            border-top: 0.45vmin dotted white; } }
  .app-header a {
    color: white;
    padding: 0.2em; }
    .app-header a:focus {
      outline-color: white; }

.contact-me {
  flex-direction: column;
  margin-bottom: auto;
  margin-top: calc(1rem + 3vmin); }
  .contact-me:before {
    border-top: 1px solid white;
    content: "";
    display: block;
    margin: 0 auto calc(1rem + 3vmin);
    width: 2rem; }
    @media (min-width: 768px) {
      .contact-me:before {
        border-top: 0.45vmin dotted white; } }
  @media (min-width: 768px) {
    .contact-me {
      flex-direction: row;
      font-size: 80%;
      margin-top: 0; }
      .contact-me:before {
        border: 0 none; } }
  .contact-me li {
    margin: 0 0 0.5rem; }
    @media (min-width: 768px) {
      .contact-me li {
        margin: 0.5rem 0 0; }
        .contact-me li:after {
          content: "\2022";
          font-size: 110%;
          margin: 0 -1rem 0 1rem; }
        .contact-me li:last-child:after {
          display: none; } }

.app-main-nav ul li {
  align-items: center;
  display: flex;
  margin: 0; }
  .app-main-nav ul li:after {
    content: "|";
    font-size: 50%;
    margin: 0 0 0 4vmin; }
  .app-main-nav ul li:last-child:after {
    display: none; }

/* Styles specific to the about section */
.passions {
  background-color: rgba(0, 0, 0, 0.05);
  margin: 5% -5.55%;
  padding: 5%; }
  .passions h3 {
    text-align: center; }

.things-I-love li {
  background-color: rgba(0, 0, 0, 0.05);
  padding: 2rem 1.5rem; }

.fun-fact h3,
.fun-fact p {
  text-align: center; }

.favorite-colors li {
  flex-basis: 8%;
  padding: 1.5rem 2rem;
  width: 50%; }
  .favorite-colors li:hover h4,
  .favorite-colors li:hover p {
    opacity: 1; }

.favorite-colors h4 {
  text-transform: capitalize; }

.favorite-colors h4,
.favorite-colors p {
  margin: 0.8rem 0;
  opacity: 0;
  -webkit-transition: opacity 300ms ease;
  transition: opacity 300ms ease; }

/* Styles specific to the work section */
.work {
  background-color: #ffca95; }
  .work a {
    color: #ad0a1a; }
    .work a:hover, .work a:focus {
      color: #45149b; }

/* Styles specific to the resume section */
.print-resume {
  flex-direction: column; }

.resume > section {
  max-width: 560px; }
  @media (min-width: 768px) {
    .resume > section.experience {
      max-width: 900px; } }

.resume h3 {
  margin-top: calc(1rem + 3vmin);
  text-align: center; }
  .resume h3:before {
    border-top: 1px solid black;
    content: "";
    display: block;
    margin: 0 auto calc(1rem + 3vmin);
    width: 2rem; }
    @media (min-width: 768px) {
      .resume h3:before {
        border-top: 0.45vmin dotted black; } }

.resume article {
  border-top: 3px solid rgba(0, 0, 0, 0.05);
  margin: 0 -1em;
  padding: 1em;
  -webkit-transition: all 300ms ease;
  transition: all 300ms ease; }
  .resume article:first-of-type {
    border-top: 0 none; }
  .resume article:hover {
    background-color: rgba(0, 0, 0, 0.05);
    border-color: transparent; }
  .resume article h3,
  .resume article h4,
  .resume article h5,
  .resume article .date {
    margin: 0; }
  .resume article .full-date {
    display: inline-block;
    font-size: 70%;
    font-weight: 700; }
  @media (min-width: 768px) {
    .resume article {
      display: grid;
      grid-template-columns: 34% 62%;
      grid-template-rows: auto auto 1fr;
      grid-column-gap: 4%; }
      .resume article p:not(.date) {
        grid-column: 2;
        grid-row: 1 / 4;
        margin: 0; }
      .resume article .full-date {
        margin-bottom: 1em; }
      .resume article.matrix {
        grid-template-rows: -webkit-max-content -webkit-max-content auto -webkit-max-content auto;
        grid-template-rows: max-content max-content auto max-content auto; }
        .resume article.matrix p:not(.date) {
          grid-column: 2;
          grid-row: 1 / 4;
          margin-bottom: 1em; }
          .resume article.matrix p:not(.date) ~ p:not(.date) {
            grid-column: 2;
            grid-row: 4 / 6;
            margin-bottom: 0; } }

.education h4,
.education p {
  margin: 0; }

.speaking ul {
  list-style-type: none;
  padding: 0; }

